@import "C:/Users/eloah/Documents/nomadgram/frontend/src/config/_variables.scss";
.container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 16px;
  width: 100%;
  box-sizing: border-box;
  border-bottom: 1px solid $light-grey;
  &:last-child {
    border: 0;
  }
  @include breakpoint("phone-tablet") {
    border-bottom: 0;
  }
  .column {
    display: flex;
    align-items: center;
    .user {
      margin-left: 10px;
      .username {
        font-weight: 600;
        display: block;
        margin-bottom: 3px;
      }
      .name {
        color: $dark-grey;
      }
    }
    .big-avatar {
      width: 55px;
      border-radius: 50%;
    }
    .avatar {
      width: 30px;
      border-radius: 50%;
    }
    .button {
      @include blueButton();
    }
  }
}



